import rough from "./rough";
let audioElement,
  audioCtx,
  analyser,
  source,
  bufferLength,
  dataArray,
  frameID,
  rc,
  canvasCtx;
//,
function drawShadow(id) {
  const canvas = document.getElementById(id);
  canvas.width = 350;
  canvas.height = 500;
  let canvasCtx = canvas.getContext("2d");
  canvasCtx.translate(25, 200);
  let rc = rough.canvas(document.getElementById(id));
  //layout
  rc.rectangle(5, 5, 300, 200, {
    strokeWidth: 5,
    fill: "#17120794",
    fillStyle: "solid" // solid fill
  });
  rc.line(5, 205, 105, 105);
  // button
  rc.rectangle(45, -15, 40, 20, {
    fill: "#17120794"
  });
  rc.rectangle(230, -15, 24, 20, {
    fill: "#17120794"
  });
  //view
  rc.rectangle(150, 30, 130, 80);
  // footer
  rc.rectangle(30, 207, 20, 10, { strokeWidth: 10 });
  rc.rectangle(230, 207, 20, 10, { strokeWidth: 10 });
}

function initAudio() {
  audioElement = document.querySelector("audio");
  // ,audioContext
  audioElement.crossOrigin = "anonymous";
  audioCtx = new AudioContext();
  //
  analyser = audioCtx.createAnalyser();
  //
  source = audioCtx.createMediaElementSource(audioElement);
  // source=>middle
  source.connect(analyser);
  //middle=>termial
  analyser.connect(audioCtx.destination);
  // ,
  analyser.fftSize = 32;
  //
  bufferLength = analyser.frequencyBinCount;
  //8item,bufferLengtharray
  dataArray = new Uint8Array(bufferLength);
  const canvas = document.getElementById("canvas");
  canvas.width = 350;
  canvas.height = 500;
  canvasCtx = canvas.getContext("2d");
  canvasCtx.translate(25, 200);
  rc = rough.canvas(document.getElementById("canvas"));
  //layout
  rc.rectangle(5, 5, 300, 200, { strokeWidth: 5 });

  //view
  rc.rectangle(150, 30, 130, 80);
  // footer
  rc.rectangle(30, 207, 20, 10, { strokeWidth: 10 });
  rc.rectangle(230, 207, 20, 10, { strokeWidth: 10 });
  //soundMf
  // rc.circle(70, 100, 100, {
  //   fill: "#24292ee0",
  //   fillStyle: "solid"
  // });
  rc.polygon(heartShape(20, 70, 80),{strokeWidth: 2 , stroke: '#ffff00cf',fillStyle: "cross-hatch",fill:'#ffff00cf',fillWeight:3 })
  drawShadow("canvas-shadow");
}
function drawBar() {
  canvasCtx.clearRect(150, 30, 130, 80);
  const bar_w = 130 / bufferLength;
  for (let i = 0; i < bufferLength; i++) {
    let bar_x = 150 + i * bar_w;
    let bar_h = ((dataArray[i] / 255) * 80) / 2;
    let bar_y = 30 + 80 - bar_h;

    rc.rectangle(bar_x, bar_y, bar_w, bar_h, { fill: "green" });
  }
}

//run it
function rockMusic() {
  // requestAnimationFrame ,canvas
  frameID = requestAnimationFrame(rockMusic);
  analyser.getByteFrequencyData(dataArray);
  drawBar();
}
// window.onload = initAudio;
// audioElement.onplay = () => audioCtx.resume().then(() => rockMusic());
// document
// .querySelector("button")
// .addEventListener("click", () => audioElement.play());
function getAudioCtx() {
  return audioCtx;
}
function getFrameID() {
  return frameID;
}
export default {
  initAudio,
  rockMusic,
  getAudioCtx,
  getFrameID
};

function heartShape(r, dx, dy) { //r:;dx:;dy:;
  var m, n, x, y, i;
  let arr = [];
  for (i = 0; i <= 7.9; i += 0.04) {
    m = i;
    n = -r * (((Math.sin(i) * Math.sqrt(Math.abs(Math.cos(i)))) / (Math.sin(i) + 1.4)) - 2 * Math.sin(i) +
      2);
    x = n * Math.cos(m) + dx;
    y = n * Math.sin(m) + dy;
    arr.push([x, y])
  }
  return arr
}